"use client";
import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';

function Login() {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const navigate = useNavigate();

  const isValid = username.trim() !== '' && password.trim() !== '';

  const handleLogin = () => {
    alert(`Login dengan:\nUsername: ${username}\nPassword: ${password}`);
    navigate('/');  
  };

  const styles = {
    box: {
      width: 320,
      margin: '80px auto',
      padding: 40,
      backgroundColor: '#FFFBEB', 
      boxShadow: '0 0 15px rgba(0, 0, 0, 0.5)', 
      borderRadius: 8,
      fontFamily: 'Arial, sans-serif',
      textAlign: 'center',
    },
    input: {
      width: '100%',
      padding: 12,
      marginBottom: 15,
      borderRadius: 8,
      border: '2px solidrgb(151, 116, 88)',
      fontSize: 14,
      boxSizing: 'border-box',
    

    },
    button: {
      width: '100%',
      padding: 12,
      borderRadius: 8,
      border: 'none',
      fontWeight: 'bold',
      fontSize: 16,
      cursor: isValid ? 'pointer' : 'not-allowed',
      backgroundColor: isValid ? '#FF6F61' : '#FFC1B6', 
      color: '#fff',
      transition: 'background-color 0.3s ease',

    },
    forgot: {
      display: 'inline-block',
      marginTop: 16,
      fontSize: 13,
      color: '#42A5F5', 
      textDecoration: 'none',
      cursor: 'pointer',
    },
  };

  return (
    <div style={styles.box}>
      <h2>Login</h2>
      <input
        style={styles.input}
        type="text"
        placeholder="Username"
        value={username}
        onChange={e => setUsername(e.target.value)}
      />
      <input
        style={styles.input}
        type="password"
        placeholder="Password"
        value={password}
        onChange={e => setPassword(e.target.value)}
      />
      <button
        onClick={handleLogin}
        disabled={!isValid}
        style={styles.button}
      >
        Login
      </button>
      {/* <a href="#" style={styles.forgot}>
        Forgot Password?
      </a> */}
    </div>
  );
}

export default Login;

